<template>
  <div class="audio-player-container">
    <!-- Êí≠ÊîæÂô®Âç°Áâá -->
    <div class="player-card">
      <!-- È°∂ÈÉ®Ë£ÖÈ•∞ -->
      <div class="player-header">
        <div class="header-glow"></div>
      </div>
      
      <!-- ‰∏ìËæëÂ∞ÅÈù¢Âå∫Âüü -->
      <div class="album-cover-container">
        <div class="vinyl-spin" :class="{ playing: isPlaying }">
          <img 
            :src="currentTrack.cover" 
            alt="‰∏ìËæëÂ∞ÅÈù¢" 
            class="album-art"
          >
          <div class="vinyl-center"></div>
          <div class="vinyl-lines"></div>
        </div>
      </div>
      
      <!-- Ê≠åÊõ≤‰ø°ÊÅØ -->
      <div class="song-info">
        <h1 class="song-title">{{ currentTrack.title }}</h1>
        <p class="song-artist">{{ currentTrack.artist }}</p>
      </div>
      
      <!-- ËøõÂ∫¶Êù° -->
      <div class="progress-container">
        <span class="time current-time">{{ formatTime(currentTime) }}</span>
        <div class="progress-bar" @click="setProgress($event)">
          <div 
            class="progress-fill" 
            :style="{ width: progress + '%' }"
          >
            <div class="progress-handle" :style="{ left: progress + '%' }"></div>
          </div>
        </div>
        <span class="time duration">{{ formatTime(duration) }}</span>
      </div>
      
      <!-- ÊéßÂà∂ÊåâÈíÆÔºà‰ΩøÁî®Á¨¶Âè∑Ôºâ -->
      <div class="controls">
        <button class="control-btn playlist-btn" @click="togglePlaylist" title="Êí≠ÊîæÂàóË°®">
          ‚ò∞
        </button>
      
        <button class="control-btn prev-btn" @click="prevTrack" title="‰∏ä‰∏ÄÈ¶ñ">
          ‚èÆ
        </button>
        <button class="control-btn play-btn" @click="togglePlay" title="Êí≠Êîæ/ÊöÇÂÅú">
          {{ isPlaying ? '‚è∏' : '‚ñ∂' }}
        </button>
        <button class="control-btn next-btn" @click="nextTrack" title="‰∏ã‰∏ÄÈ¶ñ">
          ‚è≠
        </button>
      
        <button class="control-btn repeat-btn" @click="toggleRepeat" :title="getRepeatTitle()">
          {{ repeatMode === 'none' ? '‚Ü∫' : repeatMode === 'all' ? '‚Üª' : '1‚Üª' }}
        </button>
      </div>
      
      <!-- Èü≥ÈáèÊéßÂà∂ÊªëÂùó -->
      <div class="volume-control">
        <span>{{ getVolumeIcon() }}</span>
        <input 
          type="range" 
          min="0" 
          max="100" 
          v-model="volume" 
          class="volume-slider"
          @input="setVolume"
          title="Èü≥ÈáèË∞ÉËäÇ"
        >
      </div>
      
      <!-- Êí≠ÊîæÂàóË°® -->
      <div class="playlist-container" :class="{ open: isPlaylistOpen }">
        <div class="playlist-header">
          <h3>Êí≠ÊîæÂàóË°®</h3>
          <button class="close-btn" @click="togglePlaylist" title="ÂÖ≥Èó≠">
            ‚úï
          </button>
        </div>
        <ul class="playlist-list">
          <li 
            v-for="(track, index) in playlist" 
            :key="index"
            @click="playTrack(index)"
            :class="{ active: index === currentTrackIndex }"
          >
            <span class="track-number">{{ index + 1 }}</span>
            <div class="track-info">
              <span class="track-title">{{ track.title }}</span>
              <span class="track-artist">{{ track.artist }}</span>
            </div>
            <span class="track-play-icon">{{ index === currentTrackIndex && isPlaying ? '‚è∏' : '‚ñ∂' }}</span>
          </li>
        </ul>
      </div>
    </div>
    
    <!-- Èü≥È¢ëÂÖÉÁ¥† -->
    <audio 
      ref="audio" 
      :src="currentTrack.src" 
      @timeupdate="updateProgress"
      @ended="handleEnd"
      @loadedmetadata="onLoadedMetadata"
      :volume="volume / 100"
    >
      ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅÈü≥È¢ëÊí≠Êîæ
    </audio>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // Êí≠ÊîæÁä∂ÊÄÅ
      isPlaying: false,
      progress: 0,
      currentTime: 0,
      duration: 0,
      volume: 80,
      
      // Êí≠ÊîæÊ®°Âºè
      repeatMode: 'none', // 'none', 'all', 'one'
      isPlaylistOpen: false,
      
      // Êí≠ÊîæÂàóË°®
      playlist: [
        {
          title: 'HOME SWEET HOME',
          artist: 'G-DRAGON',
          src: require('@/assets/HOME SWEET HOME.mp3'),
          cover: require('@/assets/HOME SWEET HOME.png')
        },
        {
          title: 'POWER',
          artist: 'G-DRAGON',
          src: require('@/assets/POWER.mp3'),
          cover: require('@/assets/POWER.png')
        },
        
        {
          title: 'TAKE ME',
          artist: 'G-DRAGON',
          src: require('@/assets/TAKEME.mp3'),
          cover: require('@/assets/TAKEME.png')
        },
        {
          title: 'DRAMA',
          artist: 'G-DRAGON',
          src: require('@/assets/DRAMA.mp3'),
          cover: require('@/assets/DRAMA.png')
        },
        {
          title: 'Work',
          artist: 'Rihanna',
          src: require('@/assets/Work.mp3'),
          cover: require('@/assets/Work.png')
        },
       {
          title: 'Call It What You Want',
          artist: 'Taylor Swift',
          src: require('@/assets/Call It What You Want.mp3'),
          cover: require('@/assets/Call It What You Want.png')
        }
      ],
      currentTrackIndex: 0
    };
  },
  computed: {
    currentTrack() {
      return this.playlist[this.currentTrackIndex];
    }
  },
   methods: {
    // ÂàáÊç¢Êí≠Êîæ/ÊöÇÂÅú
    togglePlay() {
      const audio = this.$refs.audio;
      if (this.isPlaying) {
        audio.pause();
      } else {
        audio.play().catch(err => {
          console.error('Êí≠ÊîæÂ§±Ë¥•:', err);
        });
      }
      this.isPlaying = !this.isPlaying;
    },
    
    // Êõ¥Êñ∞ËøõÂ∫¶Êù°
    updateProgress() {
      const audio = this.$refs.audio;
      this.currentTime = audio.currentTime;
      this.progress = (audio.currentTime / audio.duration) * 100;
    },
    
    // ËÆæÁΩÆÊí≠ÊîæËøõÂ∫¶
    setProgress(event) {
      const audio = this.$refs.audio;
      const progressBar = event.currentTarget;
      const rect = progressBar.getBoundingClientRect();
      const clickPosition = event.clientX - rect.left;
      const progressPercent = (clickPosition / rect.width) * 100;
      
      this.progress = progressPercent;
      audio.currentTime = (progressPercent / 100) * audio.duration;
    },
    
    // Â§ÑÁêÜÊí≠ÊîæÁªìÊùü
     handleEnd() {
      if (this.repeatMode === 'one') {
        // ÂçïÊõ≤Âæ™ÁéØ
        this.$refs.audio.currentTime = 0;
        this.$refs.audio.play().catch(err => console.error('Êí≠ÊîæÂ§±Ë¥•:', err));
      } else {
        // Êí≠Êîæ‰∏ã‰∏ÄÈ¶ñ
        this.nextTrack();
        if (this.repeatMode !== 'all' && this.currentTrackIndex === 0) {
          this.isPlaying = false;
        }
      }
    },
    
    
    // Ê†ºÂºèÂåñÊó∂Èó¥ÔºàÁßí -> mm:ssÔºâ
    formatTime(seconds) {
      if (isNaN(seconds)) return '00:00';
      const mins = Math.floor(seconds / 60);
      const secs = Math.floor(seconds % 60);
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    },
    
    // ËÆæÁΩÆÈü≥Èáè
    setVolume() {
      this.$refs.audio.volume = this.volume / 100;
    },
    
    // Â¢ûÂä†Èü≥Èáè
    increaseVolume() {
      this.volume = Math.min(this.volume + 5, 100);
      this.setVolume();
    },
    
    // ÂáèÂ∞èÈü≥Èáè
    decreaseVolume() {
      this.volume = Math.max(this.volume - 5, 0);
      this.setVolume();
    },
    
    // Ëé∑ÂèñÈü≥ÈáèÂõæÊ†á
    getVolumeIcon() {
      if (this.volume === 0) return 'üîá';
      if (this.volume < 50) return 'üîâ';
      return 'üîä';
    },
    
    // Ëé∑ÂèñÂæ™ÁéØÊ®°ÂºèÊ†áÈ¢ò
    getRepeatTitle() {
      if (this.repeatMode === 'none') return '‰∏çÂæ™ÁéØ';
      if (this.repeatMode === 'all') return 'ÂàóË°®Âæ™ÁéØ';
      return 'ÂçïÊõ≤Âæ™ÁéØ';
    },
    
    // ÂàáÊç¢Âæ™ÁéØÊ®°Âºè
    toggleRepeat() {
      const modes = ['none', 'all', 'one'];
      const currentIndex = modes.indexOf(this.repeatMode);
      this.repeatMode = modes[(currentIndex + 1) % modes.length];
    },
    
    // ‰∏ä‰∏ÄÈ¶ñ
     prevTrack() {
      const audio = this.$refs.audio;
      const wasPlaying = this.isPlaying;
      audio.pause(); // ÂÖàÊöÇÂÅúÂΩìÂâçÊí≠Êîæ
      
      this.currentTrackIndex = (this.currentTrackIndex - 1 + this.playlist.length) % this.playlist.length;
      this.loadAndPlay(wasPlaying);
    },
    
    // ‰∏ã‰∏ÄÈ¶ñ
    nextTrack() {
      const audio = this.$refs.audio;
      const wasPlaying = this.isPlaying;
      audio.pause(); // ÂÖàÊöÇÂÅúÂΩìÂâçÊí≠Êîæ
      
      this.currentTrackIndex = (this.currentTrackIndex + 1) % this.playlist.length;
      this.loadAndPlay(wasPlaying);
    },
    
    
     // Êí≠ÊîæÊåáÂÆöÊõ≤ÁõÆ
    playTrack(index) {
      const audio = this.$refs.audio;
      audio.pause();
      
      this.currentTrackIndex = index;
      this.loadAndPlay(true); // Âº∫Âà∂Êí≠Êîæ
      this.isPlaylistOpen = false;
    },
    
      // Âä†ËΩΩÂπ∂Êí≠ÊîæÊõ≤ÁõÆÔºàÊ†∏ÂøÉ‰ºòÂåñÔºâ
    loadAndPlay(shouldPlay) {
      const audio = this.$refs.audio;
      
      // Ê∏ÖÈô§‰πãÂâçÁöÑ‰∫ã‰ª∂ÁõëÂê¨
      audio.removeEventListener('canplaythrough', this.handleReady);
      
      // ‰øùÂ≠òÊí≠ÊîæÁä∂ÊÄÅ
      this._shouldPlay = shouldPlay;
      
      // ÁªëÂÆöÂä†ËΩΩÂÆåÊàê‰∫ã‰ª∂
      audio.addEventListener('canplaythrough', this.handleReady);
      
      // Âä†ËΩΩÊñ∞Èü≥È¢ë
      audio.src = this.currentTrack.src;
      audio.load();
    },
    
    // Èü≥È¢ëÂáÜÂ§áÂ∞±Áª™ÂêéÊí≠Êîæ
    handleReady() {
      const audio = this.$refs.audio;
      if (this._shouldPlay) {
        audio.play().catch(err => {
          console.error('Êí≠ÊîæÂ§±Ë¥•:', err);
          this.isPlaying = false;
        });
        this.isPlaying = true;
      } else {
        this.isPlaying = false;
      }
      // Ê∏ÖÈô§‰∏¥Êó∂ÂèòÈáè
      this._shouldPlay = null;
    },
    
    // Èü≥È¢ëÂÖÉÊï∞ÊçÆÂä†ËΩΩÂÆåÊàê
    onLoadedMetadata() {
      this.duration = this.$refs.audio.duration;
    },
    
    // ÂàáÊç¢Êí≠ÊîæÂàóË°®ÊòæÁ§∫/ÈöêËóè
    togglePlaylist() {
      this.isPlaylistOpen = !this.isPlaylistOpen;
    }
  },
   mounted() {
    // ÁªëÂÆö‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞ÁöÑthis‰∏ä‰∏ãÊñá
    this.handleReady = this.handleReady.bind(this);
    
    // ÈîÆÁõòÂø´Êç∑ÈîÆ
    document.addEventListener('keydown', (e) => {
      if (e.target.tagName === 'INPUT') return;
      
      switch(e.key) {
        case ' ': this.togglePlay(); e.preventDefault(); break;
        case 'ArrowRight': this.nextTrack(); break;
        case 'ArrowLeft': this.prevTrack(); break;
        case 'ArrowUp': this.increaseVolume(); e.preventDefault(); break;
        case 'ArrowDown': this.decreaseVolume(); e.preventDefault(); break;
        case 'l': this.togglePlaylist(); break;
      }
    });
  },
  beforeUnmount() {
    // Ê∏ÖÈô§‰∫ã‰ª∂ÁõëÂê¨
    this.$refs.audio.removeEventListener('canplaythrough', this.handleReady);
  }
};
</script>

<style scoped>
/* Âü∫Á°ÄÊ†∑Âºè */
.audio-player-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  padding: 20px;
  box-sizing: border-box;
  font-family: 'Segoe UI', sans-serif;
}

.player-card {
  width: 100%;
  max-width: 420px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border-radius: 24px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* È°∂ÈÉ®Ë£ÖÈ•∞ */
.player-header {
  height: 8px;
  background: linear-gradient(90deg, #ff416c, #ff4b2b);
  position: relative;
}

.header-glow {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: linear-gradient(90deg, #ff416c, #ff4b2b);
  filter: blur(6px);
  opacity: 0.7;
}

/* ‰∏ìËæëÂ∞ÅÈù¢ */
.album-cover-container {
  display: flex;
  justify-content: center;
  padding: 30px 0;
}

.vinyl-spin {
  width: 220px;
  height: 220px;
  border-radius: 50%;
  position: relative;
}

.album-art {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  border: 8px solid rgba(255, 255, 255, 0.08);
}

.vinyl-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #121212;
  z-index: 2;
  border: 4px solid rgba(255, 255, 255, 0.1);
}

.vinyl-lines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: 
    radial-gradient(circle, transparent 45%, rgba(0, 0, 0, 0.1) 45%, rgba(0, 0, 0, 0.1) 50%, transparent 50%),
    radial-gradient(circle, transparent 70%, rgba(0, 0, 0, 0.1) 70%, rgba(0, 0, 0, 0.1) 75%, transparent 75%);
}

/* ÊóãËΩ¨Âä®Áîª */
.playing {
  animation: spin 20s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Ê≠åÊõ≤‰ø°ÊÅØ */
.song-info {
  text-align: center;
  padding: 0 20px 20px;
  color: #fff;
}

.song-title {
  font-size: 24px;
  margin: 0 0 8px;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.song-artist {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  margin: 0;
}

/* ËøõÂ∫¶Êù° */
.progress-container {
  padding: 0 30px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.time {
  color: rgba(255, 255, 255, 0.7);
  font-size: 13px;
  width: 48px;
}

.current-time {
  text-align: right;
}

.progress-bar {
  flex-grow: 1;
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  position: relative;
  cursor: pointer;
}

.progress-bar:hover {
  background: rgba(255, 255, 255, 0.2);
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #ff416c, #ff4b2b);
  border-radius: 2px;
  transition: width 0.1s linear;
}

.progress-handle {
  position: absolute;
  top: 50%;
  right: -6px;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 0 10px rgba(255, 65, 108, 0.6);
  opacity: 0;
  transition: opacity 0.2s ease;
}

.progress-bar:hover .progress-handle {
  opacity: 1;
}

/* ÊéßÂà∂ÊåâÈíÆÔºàÁ¨¶Âè∑ÊåâÈíÆÔºâ */
.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding: 0 20px 20px;
}

.control-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  border-radius: 50%;
  font-size: 18px;
}

.control-btn:hover {
  color: #fff;
  transform: translateY(-2px);
}

.playlist-btn, .repeat-btn, .volume-up-btn, .volume-down-btn {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.05);
}

.playlist-btn:hover, .repeat-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.prev-btn, .next-btn {
  width: 50px;
  height: 50px;
  font-size: 22px;
  background: rgba(255, 255, 255, 0.08);
}

.play-btn {
  width: 70px;
  height: 70px;
  font-size: 28px;
  background: linear-gradient(135deg, #ff416c, #ff4b2b);
  box-shadow: 0 5px 15px rgba(255, 65, 108, 0.4);
  color: white;
}

.play-btn:hover {
  transform: scale(1.05) translateY(-2px);
  box-shadow: 0 7px 20px rgba(255, 65, 108, 0.5);
}

/* Èü≥ÈáèÊéßÂà∂ */
.volume-control {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0 30px 25px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 20px;
}

.volume-slider {
  flex-grow: 1;
  height: 4px;
  -webkit-appearance: none;
  appearance: none;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  outline: none;
}

.volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

/* Êí≠ÊîæÂàóË°® */
.playlist-container {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(15, 30, 50, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 24px;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 10;
  overflow: hidden;
}

.playlist-container.open {
  transform: translateX(0);
}

.playlist-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.playlist-header h3 {
  color: white;
  margin: 0;
  font-size: 18px;
}

.close-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 20px;
}

.close-btn:hover {
  color: white;
}

.playlist-list {
  list-style: none;
  margin: 0;
  padding: 0;
  height: calc(100% - 70px);
  overflow-y: auto;
}

.playlist-list li {
  padding: 12px 30px;
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
  transition: background 0.2s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.playlist-list li:hover {
  background: rgba(255, 255, 255, 0.05);
}

.playlist-list li.active {
  background: rgba(255, 65, 108, 0.1);
}

.track-number {
  color: rgba(255, 255, 255, 0.5);
  width: 20px;
  text-align: center;
}

.track-info {
  flex-grow: 1;
  overflow: hidden;
}

.track-title {
  display: block;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 3px;
}

.track-artist {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  font-size: 12px;
}

.track-play-icon {
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
}

.playlist-list li.active .track-play-icon {
  color: #ff416c;
}

/* ÂìçÂ∫îÂºèË∞ÉÊï¥ */
@media (max-width: 400px) {
  .vinyl-spin {
    width: 180px;
    height: 180px;
  }
  
  .controls {
    gap: 8px;
  }
  
  .play-btn {
    width: 60px;
    height: 60px;
    font-size: 24px;
  }
  
  .prev-btn, .next-btn {
    width: 45px;
    height: 45px;
    font-size: 18px;
  }
}
</style>
    


